@import url(https://fonts.googleapis.com/css2?family=Ubuntu&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: lightblue;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  padding: 0;
  margin: 0;
}
nav {
  position: relative;
  z-index: 10;
  left: 0;
  right: 0;
  top: 0;
  font-family: "Ubuntu", sans-serif;
  padding: 0 5%;
  height: 100px;
  background-color: #f9f9fa;
}
nav .logo {
  float: left;
  width: 40%;
  height: 100%;
  display: flex;
  align-items: center;
  font-size: 30px;
  color: black;
  font-weight: bolder;
}
nav .links {
  float: right;
  padding: 0;
  margin: 0;
  width: 60%;
  height: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
nav .links li {
  list-style: none;
}
nav .links a {
  display: block;
  padding: 1em;
  font-size: 23px;
  font-weight: bold;
  color: black;
  text-decoration: none;
}
#nav-toggle {
  position: absolute;
  top: -100px;
}
nav .icon-burger {
  display: none;
  position: absolute;
  right: 5%;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}
nav .icon-burger .line {
  width: 30px;
  height: 5px;
  background-color: black;
  margin: 5px;
  border-radius: 3px;
  transition: all 0.3s ease-in-out;
}
@media screen and (max-width: 768px) {
  nav .logo {
    float: none;
    width: auto;
    justify-content: center;
    font-size: 25px;
  }
  nav .links {
    float: none;
    position: fixed;
    z-index: 9;
    left: 0;
    right: 0;
    top: 100px;
    bottom: 100%;
    width: auto;
    height: auto;
    flex-direction: column;
    justify-content: space-evenly;
    background-color: #fff;
    overflow: hidden;
    box-sizing: border-box;
    transition: all 0.5s ease-in-out;
  }
  nav .links a {
    font-size: 20px;
  }
  nav :checked ~ .links {
    bottom: 0;
  }
  nav .icon-burger {
    cursor: pointer;
    display: block;
  }
  nav :checked ~ .icon-burger .line:nth-child(1) {
    -webkit-transform: translateY(10px) rotate(225deg);
            transform: translateY(10px) rotate(225deg);
  }
  nav :checked ~ .icon-burger .line:nth-child(3) {
    -webkit-transform: translateY(-10px) rotate(-225deg);
            transform: translateY(-10px) rotate(-225deg);
  }
  nav :checked ~ .icon-burger .line:nth-child(2) {
    opacity: 0;
  }
}

.footer {
  width: 100%;
}
.subfooter {
  display: flex;
  flex-direction: row;
  background-color: #1f1e28;
}
.subfooter ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  margin: 20px;
}
.subfooter ul li {
  margin: 10px;
  list-style: none;
}
.subfooter ul li a {
  display: flex;
  align-items: center;
  list-style: none;
  font-weight: 600;
  color: white;
  text-decoration: none;
  font-size: 1.25em;
  width: 200px;
}
.subfooter ul li a img {
  margin-right: 10px;
}
.subfooter2 {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  background-color: #18171d;
  color: #515f73;
}
.subfooter2 .menu {
  margin: 20px;
}
.subfooter2 .menu a {
  margin: 5px;
  text-decoration: none;
  color: #515f73;
  font-size: 1.25em;
  font-weight: 600;
}
.subfooter2 .end {
  margin: 10px;
  color: #515f73;
}
.subfooter2 .end a {
  color: white;
  text-decoration: none;
}

.about-title {
  font-size: 3em;
  text-align: center;
}
.cont {
  display: flex;
  justify-content: space-around;
}
.cont .left {
  position: sticky;
  position: -webkit-sticky;
  top: 10px;
  width: 250px;
  height: 100vh;
  background-color: #852747;
  border-radius: 10px;
}
.cont .left ul {
  list-style: none;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.cont .left ul li {
  margin: 10px 0;
}
.cont .left ul li a {
  display: block;
  padding: 10px 10px;
  font-size: 2em;
  text-decoration: none;
  background-color: #a2416b;
  border-radius: 10px 0 0 10px;
  color: white;
}
.cont .left ul li a:hover {
  background-color: #852747;
}
.cont .right {
  width: 80%;
}
.cont .right .profile {
  margin: 20px 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
  text-align: center;
  background-color: #ff7777;
}
.cont .right .profile img {
  cursor: pointer;
  border-radius: 50%;
  border: 2px solid white;
  box-shadow: 3px 3px 4px grey, -1px -1px 1px white;
  transition: -webkit-transform 0.3s;
  transition: transform 0.3s;
  transition: transform 0.3s, -webkit-transform 0.3s;
}
.cont .right .profile img:active {
  -webkit-transform: scale(200%);
          transform: scale(200%);
}
.heading {
  text-align: center;
}
.para {
  margin: 10px;
  font-size: 1.5em;
  text-align: justify;
}
.Education {
  display: flex;
  flex-direction: column;
}
.Education table {
  text-align: center;
  box-sizing: border-box;
}
.Education table tbody td,
th {
  font-size: 1.5em;
  padding: 10px;
}
ul li strong {
  font-size: 18px;
}
.Skills .skill ul {
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.Skills .skill ul li {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 15px;
}
.header {
  margin: 10px;
  padding: 10px;
  background-color: white;
  border-radius: 10px;
  box-shadow: 0 2px 2px lightgrey;
}
.cn {
  font-size: 2.5em;
}
.posi {
  font-size: 1.5em;
}
@media screen and (max-width: 1080px) {
  .cont .right {
    width: 100%;
  }
  .cont .left {
    display: none;
  }
}
@media screen and (max-width: 580px) {
  .Education table tbody td,
  th {
    font-size: 1em;
  }
  .Skills .skill ul {
    flex-direction: column;
    justify-content: center;
  }
}

.main {
  background-color: inherit;
}
.main .heading {
  font-size: 3em;
  text-align: center;
  margin: 10px auto;
}
.container {
  margin: 5px;
  display: flex;
  border-radius: 10px;
  flex-wrap: wrap;
  background-color: #fff;
  justify-content: center;
  align-items: center;
}

.card-menu {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
}
.card {
  margin: 10px;
}
.card-body {
  text-decoration: none;
  color: black;
  margin: auto;
}
.menucard {
  width: 300px;
  height: 300px;
  border-radius: 10px;
  box-shadow: 0 2px 2px grey;
}
.menucard .img {
  border-radius: 10px 10px 0 0;
}
.menucard .time {
  padding: 3px 5px;
  font-size: 12px;
  text-transform: uppercase;
  margin: 10px;
}
.menucard .category {
  top: 10px;
  right: 19px;
  margin: 10px;
  color: #fff;
  border-radius: 5px;
  padding: 3px 5px;
  line-height: 13px;
  text-transform: uppercase;
  background-color: black;
}
.menucard .title {
  margin: 10px;
}
.menucard:hover {
  box-shadow: 1px 1px 10px lightgrey, -1px -1px 10px lightgrey;
}

.navbar {
  margin: 30px auto;
  width: 100%;
  height: 50px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
.navbar .btn {
  border: none;
  margin: 5px;
  padding: 10px 30px;
  text-decoration: none;
  color: #fff;
  font-weight: bold;
  background-color: orange;
  border-radius: 10px;
  border-bottom: 3px solid gray;
}
.navbar .btn:hover {
  border-bottom: 5px solid gray;
}
.navbar .btn:focus {
  border: none;
}

